<template>
  <div>
    <div v-if="!listIsEmptyComputed" class="row" id="history-row">
      <div class="col m4 s12">
        <div id="history-links-container">
          <div class="collection">
            <a
              href="#!"
              v-for="(conversationId, index) in conversations"
              :key="index"
              @click="getCurrentConversation(conversationId)"
              class="collection-item"
              :class="getLinkClass(conversationId)"
              >Conversation number {{ conversations.length - index }}</a
            >
          </div>
        </div>
      </div>
      <div class="col m8 s12" id="history-chat-panel">
        <ul class="collection with-header">
          <li class="collection-header">
            <h4>
              Conversation of
              <span id="history-date">{{ currentConversation.date }}</span>
            </h4>
          </li>
          <li
            id="history-chat-container"
            ref="messageContainerScroll"
            class="collection-item custom-grey "
          >
            <div>
              <div v-for="(message, index) in computedMessages" :key="index">
                <chat-bot-message
                  v-if="message.author == 'chatbot'"
                  :content="message.content"
                ></chat-bot-message>
                <user-message v-else :content="message.content"></user-message>
              </div>
              <div>
                <div
                  v-if="currentConversation.helpful"
                  class="card teal darken-1"
                >
                  <div class="card-content white-text">
                    <span class="card-title"
                      >You found that this conversation was helpful</span
                    >
                    <p>
                      Thank you for telling us like you did if a conversation is
                      helful or not.
                    </p>
                  </div>
                </div>
                <div v-else class="card deep-purple darken-3">
                  <div class="card-content white-text">
                    <span class="card-title"
                      >You found that this conversation was not helpful</span
                    >
                    <p>
                      If you have found a solution to your problem please
                      contact us.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div v-else class="row" id="history-row">
      <div v-if="startDelay" class="col offset-s4 s4">
        <div class="card deep-purple darken-3">
          <div class="card-content white-text">
            <span class="card-title"
              >You don't have any conversation in your history yet !
            </span>
            <p>
              Start a conversation with our chatbot, when it is finished it will
              be placed here inside your history
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserMessage from "./components/UserMessage.vue";
import ChatBotMessage from "./components/ChatBotMessage.vue";
import axios from "axios";

export default {
  name: "History",
  components: {
    UserMessage,
    ChatBotMessage
  },
  data: function() {
    return {
      conversations: [],
      currentItems: [],
      currentConversation: {
        id: -1,
        messages: [],
        date: ""
      },
      startDelay: false
    };
  },
  mounted() {
    setTimeout(() => {
      this.startDelay = true;
    }, 1000);
    axios
      .get("/user/history/keys")
      .then(response => {
        var data = response.data;
        if (
          data !== null &&
          data.conversations_ids !== undefined &&
          data.last_conversation !== undefined
        ) {
          this.conversations = data.conversations_ids;
          this.currentConversation.id = data.last_conversation.id;
          this.currentItems = data.last_conversation.content.items;
          this.currentConversation.date = data.last_conversation.created_on;

          this.currentConversation.helpful =
            response.data.last_conversation.was_helpful;
          this.scrollMessage();
        }
      })
      .catch(err => {
        alert("Error, no internet connexion.");
        console.trace(err);
      });
  },
  methods: {
    getLinkClass(id) {
      return id === this.currentConversation.id
        ? "deep-purple darken-3 active"
        : "";
    },
    getCurrentConversation(conversationId) {
      axios
        .get("/user/history/conversation/" + conversationId)
        .then(response => {
          var data = response.data;
          if (data !== null) {
            this.currentConversation.id = conversationId;
            this.currentItems = data.content.items;
            this.currentConversation.date = data.created_on;
            this.currentConversation.helpful = data.was_helpful;
            this.scrollMessage();
          }
        })
        .catch(err => {
          alert("Error, no internet connexion.");
          console.trace(err);
        });
    },
    scrollMessage() {
      this.$nextTick(function() {
        this.$refs.messageContainerScroll.scrollTop = this.$refs.messageContainerScroll.scrollHeight;
      });
    }
  },
  computed: {
    listIsEmptyComputed() {
      return this.conversations.length === 0;
    },
    computedMessages() {
      return this.currentItems.map(el => {
        var toReturn;
        if (el.type === "Q") {
          toReturn = {
            author: "Me",
            content: el.text
          };
        } else {
          toReturn = {
            author: "chatbot",
            content: el.text
          };
        }
        return toReturn;
      });
    }
  }
};
</script>

<style lang="scss">
@import "/static/materialize/css/materialize.css";
@import "/static/css/chatbot.css";
@import "/static/css/history.css";
</style>
